<template>
  <div id="app" class="flex container h-full w-full">
    <div class="flex container h-full w-full">
      <SideNav />

      <div
        class="w-full sm:w-1/3 md:w-1/2 lg:w-1/2 h-full overflow-y-scroll no-scrollbar hidden md:block"
      >
<!--        <div-->
<!--          class="px-5 py-3 border-b border-lighter flex"-->
<!--          @click="deselectAll()"-->
<!--        >-->
          <h1 class="text-xl font-bold flex-1">Messages</h1>
          <i class="fas fa-cog text-xl text-blue cursor-pointer"></i>
          <i
            @click="newMessage()"
            class="fas fa-plus-circle ml-3 text-xl text-blue cursor-pointer"
          ></i>
        </div>

        <div>
          <div
            v-if="chats.length === 0"
            class="flex flex-col items-center justify-center w-full pt-10"
          >
            <div class="w-1/2 flex flex-col items-center justify-center">
              <p class="font-bold text-lg">No chats yet</p>
              <p class="text-sm text-dark">
                Wait until someone starts a chat here.
              </p>
            </div>
          </div>
          <div
            v-for="chat in chats"
            :key="chat.id"
          >
            <div
              class="cursor-pointer"
              :class="chat.id === active.chat.id ? 'border-r-2 border-blue' : ''"
            >
              <div
                  class="cursor-pointer bg-red-200"
                  @click="() => console.log('CLICKED!')"
              >
              </div>
              <div
                class="w-full p-2 pt-1 pb-1 md:p-4 md:pt-2 md:pb-2 border-b hover:bg-lightest flex"
                @click="selectChat(chat)"
              >
                <div class="flex-none mr-2 md:mr-4 pt-1">
                  <a v-if="active.otherUser" :href="`#/${active.otherUser.username}`">
                    <img
                      :src="
                        `${active.otherUser.avatar ||
                          'default_profile.png'}`
                      "
                      class="h-12 w-12 rounded-full flex-none"
                     />
                  </a>
                </div>
                <div class="w-full truncate">
                  <div class="flex items-center w-full">
                    <p class="font-semibold">
                      {{ chat.other_user_id }}
                    </p>
                    <p v-if="active.otherUser" class="hidden md:block text-sm text-dark ml-2 truncate">
                      @{{ active.otherUser.username || "Anonymous"}}
                    </p>
                    <p class="text-sm text-dark ml-auto">
                      {{  $filters.timeago(chat.updated_at) }}
                    </p>
                  </div>

                  <p class="pb-2 truncate" v-linkify>
                    {{ chat.last_message }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        v-if="!active"
        class="w-full z-0 h-screen border-l border-r border-lighter overflow-y-scroll no-scrollbar relative flex flex-col items-center justify-center"
        style="display: grid; grid-template-rows: 53px auto"
      >
        <div
          class="top-0 left-0 w-full px-5 pt-1 pb-2 border-lighter flex bg-white"
          :class="!active ? '' : 'border-b'"
        >
          <button
            @click="gotoHome()"
            class="block md:hidden rounded-full mr-2 focus:outline-none hover:bg-lightblue"
          >
            <i class="fas fa-arrow-left text-blue"></i>
          </button>
        </div>
        <div class="flex flex-col items-center justify-center w-full">
          <div class="w-3/5">
            <p class="font-bold text-lg">You don’t have a message selected</p>
            <p class="text-sm text-dark">
              This is where you’ll see messages from people you don’t follow.
              They won’t know you’ve seen the request until you accept it.
            </p>
            <div class="flex justify-start mt-5">
              <button
                @click="newMessage()"
                type="button"
                class="h-10 px-2 text-white font-semibold bg-blue hover:bg-darkblue rounded-full"
              >
                New Message
              </button>
            </div>
          </div>
        </div>
      </div>
      <div
        class="w-full border-l border-r border-lighter h-screen"
        style="display: grid; grid-template-rows: 53px auto 53px;"
      >
        <div
          class="px-5 pt-1 pb-2 border-b border-lighter flex bg-white sticky"
          @click="deselectAll()"
        >
          <button
            @click="gotoHome()"
            class="block md:hidden rounded-full mr-2 focus:outline-none hover:bg-lightblue"
          >
            <i class="fas fa-arrow-left text-blue"></i>
          </button>
          <div class="flex-none mr-4">
            <a :href="`#/${active.chat.other_user_id}`">
              <img
                :src="`${active.otherUser.avatar || 'default_profile.png'}`"
                class="flex-none w-6 h-6 rounded-full mt-2"
               />
            </a>
          </div>
          <div class="w-full flex flex-col">
            <h1 class="text-m font-bold flex-1">{{ active.otherUser.username }}</h1>
            <p class="text-dark text-xs">@{{ active.chat.other_user_id }}</p>
          </div>
          <i
            class="fas fa-info-circle ml-3 text-xl text-blue mt-2 cursor-pointer"
          ></i>
        </div>

        <div
          class="w-full pt-5 overflow-y-scroll no-scrollbar"
          style="display: grid; grid-template-rows: auto"
          v-scroll:top="loadMore"
        >
          <div class="w-full px-5 flex flex-col justify-between">
            <div
              class="flex flex-col-reverse mt-1 h-full"
              style="min-height: 100%;"
            >
              <div ref="scrollToMe"></div>
              <div v-for="message in messages" :key="message.id">
                <!-- Me -->
                <div v-if="message.owner_id === this.ownerProfile.id">
                  <div class="flex justify-end">
                    <div
                      class="mr-2 py-3 px-4 bg-blue rounded-bl-3xl rounded-tl-3xl rounded-tr-xl text-white"
                    >
                      {{ message.text }}
                    </div>
                  </div>
                  <div class="flex justify-end w-full mb-4">
                    <p class="text-xs text-dark mr-2">
                      {{  $filters.time(message.created_at) }}
                    </p>
                  </div>
                </div>
                <!-- Other -->
                <div v-if="message.owner_id !== this.ownerProfile.id">
                  <div class="flex justify-start">
                    <div class="flex-none mr-4">
                      <a :href="`#/${message.other_user_id}`">
                        <img
                          :src="
                            `${active.otherUser.avatar || 'default_profile.png'}`
                          "
                          class="object-cover h-8 w-8 rounded-full self-end"
                         />
                      </a>
                    </div>
                    <div
                      class="ml-2 py-3 px-4 bg-lighter rounded-br-3xl rounded-tr-3xl rounded-tl-xl text-black"
                    >
                      {{ message.text }}
                    </div>
                  </div>
                  <div class="flex items-center w-full mb-4">
                    <p class="text-xs text-dark ml-14">
                      {{ $filters.time(message.created_at) }}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="bg-white px-5 p-3 border-t border-lighter flex w-full">
          <input
            ref="input"
            class="pl-5 rounded-full w-full p-2 bg-lighter text-sm focus:bg-white focus:outline-none focus:ring-2 focus:ring-blue"
            placeholder="Start a new message"
            type="search"
            v-model="text"
            v-on:keyup.enter="sendMessage()"
            :disabled="disabled"
          />
          <button
            class="rounded-full focus:outline-none right-4 ml-4 w-8 h-8"
            :class="
              `${
                text.length === 0
                  ? 'opacity-50 cursor-default'
                  : 'hover:bg-lightblue'
              }`
            "
            @click="sendMessage()"
            :disabled="disabled"
          >
            <i class="fas fa-arrow-right text-xl text-blue"></i>
          </button>
        </div>
      </div>

      <NewMessageOverlay
        v-if="showNewMessageModal"
        :showNewMessageModal.sync="showNewMessageModal"
        @selected="selected"
      />
    </div>
<!--  </div>-->
</template>

<script>
import {defineAsyncComponent} from "vue";
import {reactiveService} from "@/service/service";

export default {
  name: "Messages",
  components: {
    SideNav: defineAsyncComponent(() => import('@/components/SideNav.vue')),
    Loader: defineAsyncComponent(() => import('@/components/Loader.vue')),
    NewMessageOverlay: defineAsyncComponent(() => import('@/components/NewMessageOverlay.vue')),
  },
  data() {
    return {
      text: "",
      disabled: false,
      showNewMessageModal: false,
      chats: [],
      messages: [],
      ownerProfile: undefined,
      active: undefined,
    };
  },
  methods: {
    newMessage() {
      this.showNewMessageModal = true;
    },
    gotoHome() {
      this.$router.push({
        name: "Home",
      });
    },
    sendMessage() {
      if (!this.active.chat.other_user_id || this.text.length === 0) return;
      this.disabled = true; //disable message input temporarily
      reactiveService.sendDirectMessage({
        chatId: this.active.chat.id,
        text: this.text,
      }).then(() => {
        this.text = "";
        this.disabled = false;
        this.$nextTick(() => {
          this.scrollToEnd();
        });
      });
    },
    scrollToEnd() {
      this.$nextTick(() => {
        const el = this.$refs.scrollToMe;
        if (el) {
          el.scrollIntoView({ behavior: "smooth" });
        }
        setTimeout(() => this.$refs.input.focus(), 500);
      });
    },
    async selected(user) {
      if (!this.ownerProfile.id || !user.id) {
        return;
      }
      this.showNewMessageModal = false;
      const chatId = `${this.ownerProfile.id}:${user.id}`;
      const chatIdReversed = `${user.id}:${this.ownerProfile.id}`;

      let currentChat = undefined;
      this.chats.forEach((chat) => {
        if (chat.id === chatId || chat.id === chatIdReversed) {
          currentChat = chat;
        }
      });
      if (!currentChat) {
        currentChat = {
          id: chatId,
          other_user_id: user.id,
          owner_id: "",
        };
      }
      await this.selectChat(currentChat);
    },
    async selectChat(chat) {
      console.log("selectChat", JSON.stringify(chat));
      this.messages = [];

      const [otherUser, messagesResp] = await Promise.all([
        reactiveService.getProfile(chat.other_user_id),
        reactiveService.getDirectMessages({chatId:chat.id, fromBeginning:true}),
      ]);

      this.active = { chat, otherUser };
      this.messages = [...this.messages, ...messagesResp.messages || []];

      this.scrollToEnd();
    },
    deselectAll() {
      this.active = undefined;
    },
    async loadMore() {
      if (!this.active.chat) return;
      const messagesResp = await reactiveService.getDirectMessages(
          {chatId:this.active.chat.id, fromBeginning:false},
      )
      this.messages = [...this.messages, ...messagesResp.messages];
    },
  },
  async created() {
    console.log("loading component:", this.$options.name);
    this.ownerProfile = reactiveService.getOwnerProfile()

    const chats = await reactiveService.getChats(true)
    if (!chats || chats.length === 0) {
      console.error("No chats found.");
      return;
    }
    this.chats = chats;

    await this.selectChat(chats[0]);
  },
};
</script>
